<template>
<section>
    <template>
        <div class="modal my-popups right fade show" id="manageProjectModal" role="dialog" style="display: block">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header mheader-user">
                        <button type="button" class="close" @click="closeModal"><i class="fa fa-times" aria-hidden="true"></i> </button>
                        <h4 class="modal-title" id="myModalLabel"> {{ $t("ConfigureQuoteNumbering") }} </h4>
                    </div>
                    <!-- <div class="preview-panel">
                        <div class="input-group">
                            <div class="input-group-prepend">
                                <span id="basic-addon3" class="input-group-text">Preview</span>
                            </div>
                            <input type="text" disabled="disabled" maxlength="100" class="form-control" v-model="selectedValueForPreview" />

                        </div>
                    </div> -->
                    <div class="preview-panel">
                        <div class="input-group">
                            <div class="input-group-prepend">
                                <span id="basic-addon3" class="input-group-text">Preview</span>
                            </div>
                            <input type="text" disabled="disabled" maxlength="100" class="form-control" v-model="selectedValueForPreview">
                        </div>
                    </div>

                    <draggable v-model="ArrayData" :move="checkMove" :list="ArrayData" class="element-panel" :group="{ name: 'people', pull: 'clone', put: false }" @clone="onClone">

                        <div class="single-element mb-2 ml-3 Year " v-for="(item, index) in ArrayData" :key="index" style="cursor: pointer" :disabled="draggable">

                            <i v-bind:class="item.classicon"></i> {{ item.type }}
                        </div>
                    </draggable>

                    <div>
                        <div id="divmanualconfigsetting" class="list-group d-flex flex-wrap dragarea mt-0" style="flex-direction: unset; min-height: 105px">
                            <draggable :list="GetDragArrayData" class="element-panel" :group="{ name: 'people' }" @clone="onClone">
                                <div v-for="(item, index) in GetDragArrayData" :key="index" :class="`dragelements config-data ${item.class}`">

                                    <div class="col-12" v-if="item.type == 'Month' || item.type == 'Number' || item.type == 'Year' || item.type == 'Separator' || item.type == 'Character' || item.type =='PreviewNum'">
                                        <div :id="index" class="pickflowbase ml-cutomf-bg float-left p-2 w-100 box-warning">
                                            <div class="labeltext text-left p-0 float-left">
                                                <label>{{item.type }}</label>
                                                <div class="dropdown float-right pt-1 mr-3">
                                                    <i v-if=" item.type !== 'Character' && item.type !== 'Separator' && item.type !== 'PreviewNum'" class="fa fa-ellipsis-h text-secondary" @click="toggleDropdown(index)"></i>

                                                    <div v-if="isDropdownOpen && openedDropdownIndex === index ">
                                                        <div class="dropdown-menu panelfix_dots rounded  border-0 bg-white show">
                                                            <div class="border">
                                                                <a v-for="itemss in yearStaticValues" :key="itemss.id" href="javascript:;" class="d-flex flex-direction-column dropdown-item px-2 py-1" draggable="true" @click="handleDropdownItemClick(itemss.value)">
                                                                    <span class="w-100 p-1">{{ itemss.value }}</span>
                                                                </a></div>
                                                        </div>
                                                    </div>

                                                    <div class="dropdown-menu panelfix_dots rounded border-0 bg-white show ">
                                                        <div class="border" v-if="shouldShowDiv(item)">
                                                            <label v-if="item.type === 'Number' && isDropdownOpen && openedDropdownIndex === index" class="form-check-inline d-block m-0 mb-1 ">
                                                                <span class="label-text text-secondary mx-1" style="font-size: 14px; float: left;">Incr. By</span>
                                                                <input type="text" maxlength="2" class="form-control form-control-sm" aria-required="false" aria-invalid="false" style="width: 84px; height: 25px !important; min-height: 25px !important; margin: auto;" :value="item.value">

                                                            </label>
                                                            <label v-if="item.type === 'Number' && isDropdownOpen && openedDropdownIndex === index" class="form-check-inline d-block m-0 mb-1  ">
                                                                <span class="label-text text-secondary mx-1" style="font-size: 14px; float: left;">Reset By</span>
                                                                <select v-model="resetBy" @change="handleResetByChange" style="margin-left: 15px;">
                                                                    <option value="Month">Month</option>
                                                                    <option value="EM">Every Month</option>
                                                                    <option value="Year">Year</option>
                                                                </select>
                                                            </label>
                                                            <label v-if="resetBy === 'Month' && item.type === 'Number' && isDropdownOpen && openedDropdownIndex === index" class="form-check-inline d-block m-0 mb-1 ">
                                                                <span class="label-text text-secondary mx-1" style="font-size: 14px; float: left;">Month</span>
                                                                <select style="margin-left: 15px;" v-model="selectedMonths" @click="openMonthDropdown">
                                                                    <option value="January">Jan</option>
                                                                    <option value="February">Feb</option>
                                                                    <option value="March">Mar</option>
                                                                    <option value="April">Apr</option>
                                                                    <option value="May">May</option>
                                                                    <option value="June">June</option>
                                                                    <option value="July">July</option>
                                                                    <option value="August">Aug</option>
                                                                    <option value="September">Sep</option>
                                                                    <option value="October">Oct</option>
                                                                    <option value="November">Nov</option>
                                                                    <option value="December">Dec</option>
                                                                </select>
                                                            </label>
                                                        </div>
                                                    </div>
                                                </div>

                                                <div v-if="item.type == 'Month'">
                                                    <input type="text" disabled="disabled" class="form-control form-control-sm data1" v-model="selectedMonth" />
                                                </div>
                                                <div v-if="item.type == 'Number'">
                                                    <input type="text" class="form-control form-control-sm" v-model="selectedNumber" @input="updatePreview" />
                                                </div>

                                                <div v-if="item.type == 'Year' && item.type != 'Number'">
                                                    <input type="text" v-model="selectedValue" disabled="disabled" class="form-control form-control-sm data1" />

                                                </div>
                                                <div v-if="item.type == 'Character'">
                                                    <input type="text" class="form-control form-control-sm data1" v-model="characterInput" @input="updatePreview" />

                                                </div>
                                                <div v-if="item.type == 'Separator'">
                                                    <input type="text" class="form-control form-control-sm data1" v-model="separatorInput" @input="updatePreview" />

                                                </div>
                                                <div v-if="item.type == 'PreviewNum' ">
                                                    <input type="text" class="form-control form-control-sm data1" v-model="inputPreview" @input="updatePreview" />

                                                </div>

                                            </div>
                                            <i class="close fa fa-times" style="cursor: pointer" @click="deleteItem(index,item.type)"></i>

                                        </div>
                                    </div>
                                </div>
                            </draggable>
                        </div>
                    </div>

                    <div class="help-block">
                        <p id="divHelpText">Ticket Numbering from next Ticket</p>
                        <span>CBApr262022-0001, (...0014, ...0027, ...0040 and so on).</span>
                        <br />
                        <p id="divHelpTextAfterReset"> Ticket Numbering after reset next year</p>
                        <span>CBApr262023-0001, (...14, ...27, ...40 and so on).</span>
                    </div>
                    <div class="col-lg-12 text-right">
                        <a href="javascript:;" title="Save" @click="SavePriviewNumber(selectedValueForPreview)" class="btn btn-success formbtn widthhalf">
                            <i class="fa fa-save pr-2"></i>
                            Save
                        </a>

                        <a href="javascript:;" title="Cancel" class="btn btn-danger formbtn widthhalf">
                            <i class="fa fa-close pr-2"></i>
                            Cancel</a>
                    </div>
                </div>
            </div>
        </div>
    </template>
</section>
</template>

<script>
import DataService from "@/services/DataService";
import draggable from 'vuedraggable';

let idGlobal = 8;
export default {
    components: {
        draggable,
    },
    props: {
        id: {
            type: Number,
            required: true,
        },
    },
    data() {
        return {
            ArrayData: [],
            classicon: "",
            openedDropdownIndex: null,
            isDropdownOpen: false,
            GetDragArrayData: [],
            dragOptions: {
                group: 'draggable-elements',
                animation: 150,
            },
            yearStaticValues: [],
            selectedValue: '',
            selectedMonth: '',
            selectedValueForPreview: '',
            characterInput: '',
            separatorInput: '',
            draggable: false,
            showMonthDropdown: false,
            resetBy: '',
            selectedMonths: '',
            selectedNumber: '',
            inputPreview:''
        };
    },
    created() {
        debugger;

        var vm = this;
        console.log("id", this.id);
        vm.GetQuoteNumber(this.id);
    },

    watch: {
        ArrayData: {
            handler(newVal, oldVal) {
                if (newVal.length > 0) {
                    let items = newVal.map(item => item.type);
                    if (items.length !== new Set(items).size) {
                        // If there are duplicates, remove the duplicates
                        this.ArrayData = newVal.filter((value, index, self) => {
                            return self.findIndex(t => t.type === value.type) === index;
                        });
                        this.ShowAlert(this.$t("NoDuplicate"), 'failure', true, this.$t('Alert'));
                    }
                }
            },
            deep: true,
        },
    },
    computed: {
        selectedValueForPreview: {
            get() {
                return this.selectedNumber;
            },
            set(value) {
                
                this.selectedNumber = value;
            }
        }
    },

    methods: {
        SavePriviewNumber(selectedValueForPreview){
         debugger;
         this.$emit("receiveData",selectedValueForPreview)


        },
        shouldShowDiv(item) {
            // Define your conditions here to determine whether the div should be shown or hidden
            // if (item.type === 'Number') {
            //     // For example, hide the div if the item type is 'Number'
            //     return false;
            // }
            if (item.type === 'Separator') {
                return false
            } else if (item.type === 'Year') {
                return false
            } else if (item.type === 'Month') {
                return false
            } else if (item.type === 'PreviewNum') {
                return false
            } else if (item.type === 'Character') {
                return false
            } else {
                // Otherwise, show the div
                return true;
            }
        },
        showMonthDropdown() {
            debugger;
            return this.resetBy === 'Month';
        },
        handleResetByChange() {
            debugger
            if (this.resetBy !== 'Month') {
                this.selectedMonths = ''; // Reset the selected month if Reset By is changed
            }
        },
        openMonthDropdown() {
            debugger;
            if (this.resetBy != 'Month') {
                this.showMonthDropdown = true;
            }
        },

        deleteItem(index, type) {
            debugger;
            // this.selectedValue=''

            if (type == 'Year') {

                this.selectedValue = '';
            }
            if (type == 'Month') {

                this.selectedMonth = '';
            }
            if (type == 'Separator') {
                this.separatorInput = '';
            }
            if (type == 'Number') {
                this.selectedNumber = ''
            }
            if (type == 'Character') {
                this.characterInput = ''
            }
            if (type == 'PreviewNum') {
                this.inputPreview = ''
            }
            this.updatePreview();
            this.GetDragArrayData.splice(index, 1);

        },
        checkMove(e) {
            debugger
            var vm = this;
            // Get the dragged element
            const draggedElement = e.draggedContext.element;

            for (let i = 0; i < vm.GetDragArrayData.length; i++) {
                if (e.draggedContext.futureIndex === i && vm.GetDragArrayData[i].type === draggedElement.type) {
                    return false;
                }
                if (e.draggedContext.futureIndex === i + 1 && vm.GetDragArrayData[i].type === draggedElement.type) {
                    return false;
                }
                if (e.draggedContext.futureIndex === i - 1 && vm.GetDragArrayData[i].type === draggedElement.type) {
                    return false;
                }
            }

        },

        toggleDropdown(index) {
            debugger;
            // Toggle the dropdown for the clicked ellipsis icon
            this.openedDropdownIndex = index;
            //   this.openedDropdownIndex = this.openedDropdownIndex === index ? null : index;
            this.isDropdownOpen = !this.isDropdownOpen;
            if (this.GetDragArrayData[index].type === 'Year') {
                this.yearStaticValues = [{
                        id: 1,
                        value: 'Two Digit',
                    },
                    {
                        id: 2,
                        value: 'Four Digit'
                    }
                ];

            } else if (this.GetDragArrayData[index].type === 'Month') {
                this.yearStaticValues = [{
                        id: 1,
                        value: 'short name'
                    },
                    {
                        id: 2,
                        value: 'Full Name'
                    },
                    {
                        id: 3,
                        value: 'Month Name'
                    }

                ];
            } else if (this.GetDragArrayData[index].type === 'Number') {

                this.yearStaticValues = ''
            }
        },

        handleDropdownItemClick(value) {
            debugger;
            // Handle the click event for each dropdown item
            console.log(`Clicked on ${value}`);

            if (value == 'Two Digit') {
                this.selectedValue = 24;
            } else if (value == 'Four Digit') {
                this.selectedValue = 2024;
            } else if (value == 'short name') {
                this.selectedMonth = 'Feb'
            } else if (value == 'Full Name') {
                this.selectedMonth = 'Feburary'
            } else if (value == 'Month Name') {
                this.selectedMonth = '02'
            }
            this.updatePreview();
            this.isDropdownOpen = false; // Close the dropdown after item click
        },
        updatePreview() {
            // Combine selectedYear and selectedMonth and update the preview
            debugger;
            this.selectedValueForPreview = this.selectedValue + this.selectedMonth + this.characterInput + this.separatorInput + this.selectedNumber + this.inputPreview;

        },
        closeModal: function () {
            var vm = this;
            vm.$parent.ClosemodalConfugureQuote();
        },

        async GetQuoteNumber(id) {

            debugger;
            var vm = this;
            var params = `id=${id}`;
            // const response = await DataService.GetConfigNumberJsonData(params);

            await DataService.GetConfigNumberJsonData(params).then((response) => {
                debugger;
                if (response.data.data != '') {
                    vm.ArrayData = response.data.data;
                    vm.ArrayData.forEach(item => {
                        switch (item.type) {
                            case "Year":
                                item.classicon = "fa fa fa-calendar";
                                break;
                            case "Month":
                                item.classicon = "fa fa-calendar-o";
                                break;
                            case "Separator":
                                item.classicon = "fa fa-code";
                                break;
                            case "Character":
                                item.classicon = "fa fa-downcase-a";
                                break;
                            case "Number":
                                item.classicon = "fa fa-sort-numeric-asc";
                                break;
                            default:
                                item.classicon = "fa fa-pencil";
                        }
                    });
                } else {
                    return ("not record found")
                }

            })

            // Check if the response contains the expected data structure

        },
    },
};
</script>

<style>
.dragarea .dragelement1 .ml-cutomf-bg .labeltext {
    width: 100% !important;
}

.dragarea .dragelement1 .close {
    width: 20px;
    height: 20px;
    position: absolute;
    top: -12px;
    right: -8px;
    opacity: 1;
    line-height: 20px;
    text-shadow: none;
    font-size: 12px;
    text-align: center;
    background: rgb(123, 122, 122) !important;
    color: rgb(255, 255, 255) !important;
}

.dragarea .dragelement1 .ml-cutomf-bg {
    background: #fff !important;
    border: 1px solid #dcdcdc;
}

.dragarea .dragelements .ml-cutomf-bg .dropdown {
    position: absolute;
    right: -7px;
    top: -4px;
}

.dropdown-menu.panelfix_dots {
    right: -31px;
    left: auto;
    min-width: 205px;
}
</style>
