<template>
<div class="padding-t_10" id="reportstabsdata">
    <div class="left-menu-tab new_tab">
        <div class="col-md-12 p-0">
            <div class="theme-primary partition">
                <span class="p-name text-white">{{ $t("Configuration") }}</span>
            </div>
        </div>
        <div class="pt-2 mb-1 w-100 d-none d-lg-block">
            <div class="input-group customsearchbox">
                <input type="text" maxlength="50" placeholder="Search module" class="form-control searchcustomfilter border-right-0" autocomplete="off" v-model="searchQuery" @keyup="filteredCustomField($event)" />
                <div class="input-group-append">
                    <span class="input-group-text bg-transparent bg-white border-left-0"><i class="fa fa-search" @click="filteredCustomField"></i></span>
                </div>
            </div>
        </div>
        <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab" id="upper" role="tablist">
            <li class="nav-item" v-for="item in moduleddata" :key="item">
                <a id="leftmenu" class="nav-link left-menu " @click="activateTab(item)" :class="{ 'active': selectedModule === item }">
                    <i id="itemdata" v-bind:class="item.classicon"></i>&nbsp;
                    {{ item.moduleTitle }}
                </a>
            </li>
        </ul>

    </div>

    <!-- right-content here -->
    <div class="right-content">
        <loader :isVisible="isLoading"></loader>
        <div class="col-md-12 p-0">
            <section class="page-action action-margin">
                <div class="container-fluid p-0">
                    <div class="col-md-12 p-0">
                        <div class="theme-primary partition p-actions-expand d-flex " style="overflow: visible">
                            <div class="action-headings col-md-7">
                                <span :class="dynamicclass" class="p-name text-white pd-2" id="spnreporttext" style="margin-right: 5px;">
                                    {{ modulename }}
                                </span>

                                <span id="showDaterange" style="display: none" class="p-actions float-right p-2">

                                </span>

                                <span class="collapse-head-btns">
                                    <a href="javascript:;" class="collapse-btns"><i class="fa fa-angle-up text-white"></i></a>
                                </span>
                            </div>
                            <div class="col-md-5">
                                <div class="row" style="padding: 5px 0;">
                                </div>

                            </div>
                        </div>

                    </div>
                </div>
            </section>

        </div>

        <div class="col-md-12 p-0">
            <div class="tab-content" id="myTabContent">
                <div class="expand-list">
                    <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                        <i class="icon-solid arrow-solid-left text-white" style="font-size: 9pt"></i>
                    </a>
                </div>
                <div class="tab-pane active config_list container-fluid border pt-2" id="ulKeysContainer">
                    <Form class="col-12 p-0">
                        <div class="row">

                            <div style="z-index:99999" v-for="(item, allConfigDataIndex) in moduleKeys" class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6" :key="'configdata-1' + allConfigDataIndex">
                                <div v-if="item.coreConfigType == 'radiobutton'" class="form-group projectConfig">
                                    <label>
                                        
                                        <span> {{ item.coreConfigKeyValue }} </span>
                                        <span class="text-danger">*</span>
                                        <a href="javascript:;" data-toggle="popoveruserguide" :data-content="item.coreConfigDescription">
                                            <i class="fa fa-question-circle-o text-popover" :title="item.coreConfigDescription"></i>
                                        </a>
                                    </label>
                                    <div class="rad_value" type="radiobutton" :keys="item.coreConfigId" :keyName="item.coreConfigKeyValue" v-if="item.coreConfigType == 'radiobutton'">
                                        <label v-for="items in Getoption(item.coreConfigOption)" class="cogfiglbl form-check form-check-inline" :key="items">
                                            <div class="custom-control custom-radio custom-control-inline">
                                                <input type="radio" class="custom-control-input" :name="item.coreConfigDescription" v-model="item.coreConfigValue" :value="items" />
                                                <label class="custom-control-label" :for="'rdo_' + index"></label>
                                            </div>{{ items }}
                                        </label>
                                    </div>
                                </div>
                                <div :key="'configdata-2' + allConfigIndex">
                                    <div :keyName="item.coreConfigKey" v-if="item.coreConfigType == 'text' " class="form-group projectConfig">
                                        <label>
                                            <span> {{ item.coreConfigKeyValue }} </span>
                                            <span class="text-danger">*</span>
                                            <a href="javascript:;" :data-content="item.coreConfigDescription" data-toggle="popoveruserguide">
                                                <i class="fa fa-question-circle-o text-popover" :title="item.coreConfigDescription" style="font-size: 14px"></i>
                                            </a>
                                        </label>
                                        <div class="input-group">
                                            <input :keys="item.coreConfigId" :keyName="item.coreConfigKey" type="text" :name="item.coreConfigDescription" :value="item.coreConfigValue" class="textfield form-control" :disabled="item.coreConfigKey === 'TicketStartingNumber'" />
                                            <div v-if="item.coreConfigKey === 'TicketStartingNumber'" class="input-group-append">
                                                <span class="input-group-text">
                                                    <i class="fa fa-pencil text-info font-18" @click="modalConfugureQuote(item.coreConfigId)"></i> <!-- Font Awesome lock icon indicating disabled state -->
                                                </span>
                                            </div>
                                            <span name="LocationTracking" id="LocationTracking" class="invalid-feedback d-block">
                                            </span>
                                        </div>
                                    </div>
                                </div>
                                 <div v-if="item.coreConfigType == 'dropdown' && item.coreConfigKey=='TimeZone'">
                                  <label>
                                        <span> {{ item.coreConfigKeyValue }} </span>
                                        <span class="text-danger">*</span>
                                        <a href="javascript:;" data-toggle="popoveruserguide" :data-content="item.coreConfigDescription">
                                            <i class="fa fa-question-circle-o text-popover" :title="item.coreConfigDescription"></i>
                                        </a>
                                    </label>
                                            <select class="select form-control" >
                                                <option v-for="(item, timeZoneIndex) in timeZoneData" :value="item.timezone_id" :key="timeZoneIndex">
                                                    {{ item.display_name }}
                                                </option>
                                            </select>
                                        
                                    </div>
                                <div :keyName="item.coreConfigKey" v-if="item.coreConfigType == 'dropdown' && item.coreConfigKey!='TimeZone'" class="form-group projectConfig">
                                    <label>
                                        <span> {{ item.coreConfigKeyValue }} </span>
                                        <span class="text-danger">*</span>
                                        <a href="javascript:;" data-toggle="popoveruserguide" :data-content="item.coreConfigDescription">
                                            <i class="fa fa-question-circle-o text-popover" :title="item.coreConfigDescription"></i>
                                        </a>
                                    </label>
                                    
                                   

                                    <select :keys="item.coreConfigId" :keyName="item.coreConfigKey" class="select form-control" v-model="item.coreConfigValue" name="">
                                        <option v-for="(items, GetoptionIndex) in Getoption(item.coreConfigOption)" :value="items" :key="GetoptionIndex">
                                            {{ items }}
                                        </option>
                                    </select>
                                    <!-- <span name="EnableLocationTrackingModule" id="EnableLocationTrackingModule"
                                class="invalid-feedback d-block">{{ errors[0] }}</span>  -->
                                    <!-- </Field> -->
                                </div>
                                <div v-if="item.coreConfigType == 'checkbox'" class="form-group projectConfig" style="z-index:99">
                                    <label>
                                        <span> {{ item.coreConfigKeyValue }} </span>
                                        <span class="text-danger">*</span>
                                        <a href="javascript:;" data-toggle="popoveruserguide" :data-content="item.coreConfigDescription">
                                            <i class="fa fa-question-circle-o text-popover" :title="item.coreConfigDescription"></i>
                                        </a>
                                    </label>

                                    <div class="rad_value" type="checkbox" :keys="item.coreConfigId" :keyName="item.coreConfigKeyValue" v-if="item.coreConfigType == 'checkbox'">
                                        <label v-for="(items,index) in Getoption(item.coreConfigOption)" class="cogfiglbl form-check form-check-inline" :key="items">
                                            <div class="custom-control custom-checkbox custom-control-inline">
                                                <input type="checkbox" class="custom-control-input" :name="item.coreConfigDescription" checked="checked" :value="item.coreConfigValue" />
                                                <label class="custom-control-label" :for="'rdo_' + index"></label>
                                            </div>{{ items }}
                                        </label>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                                    <div class="form-group projectConfig" v-if="activeKey==1 && item.coreConfigKey=='CompanyLogo'">

                                        <label>
                                            <span>{{item.coreConfigKeyValue}}</span>
                                            <span class="text-danger">*</span>
                                            <a href="javascript:;" data-toggle="popoveruserguide" data-content="Company Logo">
                                                <i class="fa fa-question-circle-o text-popover" style="font-size: 14px"></i>
                                            </a>
                                        </label>
                                        <div class="row">
                                            <div class="col-lg-12 col-md-12 mb-1 mt-0 custom-file uploadimg" id="divFiles">

                                                <input type="file" name="attach" id="flFile" @change="setFile(values, meta, $event)" class="form-control" tabindex="-1" style="position: absolute; clip: rect(0px, 0px, 0px, 0px)" />

                                                <div class="bootstrap-filestyle input-group">
                                                    <!-- <Field :rules="{ required: true }" name="Company Logo" v-slot="{ errors }"> -->

                                                    <input type="text" id="CompanyLogoCoreConfigId" class="form-control disabled" v-model="attachmentDiv" disabled="" />

                                                    <!-- <span v-if="errors[0]" name="CompanyLogo" id="CompanyLogoId"
                                      class="invalid-feedback">{{ errors[0] }}</span> -->
                                                    <!-- </Field> -->

                                                    <div class="input-group-append" style="cursor: pointer">
                                                        <span class="group-span-filestyle input-group-btn input-group-text bg-white" tabindex="0"><label title="Click here to select a file." for="flFile" class="mb-0 font-weight-normal">Choose File</label></span><span class="input-group-text border-0 bg-transparent p-0 pl-2"><a @click="Clearimagevalue" href="javascript:;" class="round-icon-small btn-danger-light clsattclear" title="Clear"><i class="fa fa-undo"></i></a><a href="javascript:;" class="round-icon-small btn-danger-light clsattremove d-none" title="Remove"><i class="fa fa-times"></i></a></span>
                                                    </div>
                                                </div>
                                                <span id="spnFile" class="field-validation-valid" data-valmsg-for="FileSrc"></span>
                                            </div>
                                            <div class="col-lg-5 col-md-12 mt-0 position-relative displayimg" style="max-width: 300px; min-width: 300px">
                                                <div class="float-left" style="border: 1px solid #f2f2f2; padding: 2px 14px">
                                                    <img id="CompanyLogo" :src="blobUrl" key="29" width="200" style="max-height:70px;" alt="Image">

                                                </div>

                                                <div class="float-left">
                                                    <a href="javascript:;" @click="ClearImage" class="remove round-icon-small btn-dark">
                                                        <i class="fa fa-times" title="Remove" alt="Remove"></i>
                                                    </a>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>

                            </div>
                            <div class="col-12 mt-3">
                                <div class="row flex-row-reverse">
                                    <button v-if="showsavebutton" type="button" title="Save" @click="saveStorageConfiguration()" class="btn btn-success pr-2">
                                        <em class="fa fa-save pr-2"></em>
                                        Save
                                    </button>
                                </div>
                            </div>

                        </div>
                    </Form>

                </div>
                <modalConfugureQuote v-if="IsSearchItem" :id="id" @ClosemodalConfugureQuote="ClosemodalConfugureQuote" @receiveData="receiveData"></modalConfugureQuote>
            </div>
        </div>
    </div>
    <!-- right-content End here -->

</div>
</template>

<script>
var name = [];
import DataService from "../../services/DataService";
import modalConfugureQuote from '../../components/Common/Modal/modalConfugureQuote.vue';

export default {
    components: {
        modalConfugureQuote
    },
    props: {
        fromDate: {
            type: Object,
        },
        ToDate: {
            type: Object,
        },
    },
    data() {
        return {
            activeKey: 0,
            moduleddata: [],
            moduleddataAll: [],
            searchQuery: "",
            modulename: "",
            moduletitle: [],
            classicon: [],
            activeKey: null,
            moduleKeys: [],
            validate: [],
            saveArray: [],
            showsavebutton: false,
            classadded: '',
            isLoading: false,
            dynamicclass: '',
            selectedModule: null,
            IsSearchItem: false,
            id: null,
            attachmentDiv: '',
            blobUrl: '',
            imgPath: '',
            UserType: '',
            CompanyLogoCoreConfigId: 0,
            timeZoneData: [],
            getTimeZone: '',
            timeZoneKeys: 0,


            //  model: null
        };
    },

    created() {
        debugger
        var vm = this;

        vm.UserType = vm.GetUserInfo.UserType;
        vm.containerName = vm.GetUserInfo.ContainerName;
        vm.GetLeftModulesdata();

    },
    //    computed(){
    // this.GetBlobUrl();
    //    },

    methods: {


        receiveData(event){
            debugger;
            this.moduleKeys.forEach(item=>{
                if(item.coreConfigKey === 'TicketStartingNumber'){
                    item.coreConfigValue=event
                    this.ShowAlert(this.$t("Updatesucessfully"), 'success', true, this.$t('Alert'));
                }
            })
            this.IsSearchItem=false;

        },
        Clearimagevalue: function () {
            var vm = this;
            vm.attachmentDiv = '';
        },
        GetTimeZone: function () {
            debugger;
            var vm = this;
            DataService.GetTimeZone().then(function (response) {
                debugger;
                vm.timeZoneData = response.data;
            })
        },
        setFile: function (values, meta, e) {
            debugger;
            this.attachmentDiv = e.target.files[0].name;
            this.fileName = e.target.files[0];
            this.arrayGetFileDetails = e.target.files[0];
        },
        ClearImage: function () {
            debugger
            this.blobUrl = 'http://localhost:9000/' + '/Content/images/DefaultImages/company-logo.jpg'
            this.defaultCompanyLogo = 'COMPANY-LOGO.JPG'
        },

        GetBlobUrl: function () {
            var vm = this;
            DataService.GetBlobUrl().then((response) => {

                var url = response.data[0].CONFIG_DATA_VALUE;
                var usertype = this.UserType;
                if (vm.imgPath.toUpperCase() == "COMPANY-LOGO.JPG") {
                    debugger
                    vm.blobUrl = 'http://localhost:9000/' + '/Content/images/DefaultImages/company-logo.jpg'
                }
                if (usertype == "CA" && this.imgPath.toUpperCase() != "COMPANY-LOGO.JPG") {
                    vm.blobUrl = url + '/' + this.containerName + '/' + 'Upload' + '/' + 'CompanyLogo' + '/' + vm.imgPath
                }
            })
        },
        modalConfugureQuote: function (id) {
            debugger;
            this.id = id;
            this.IsSearchItem = true;
        },

        ClosemodalConfugureQuote: function () {
            //debugger;
            this.IsSearchItem = false;
        },

        filteredCustomField: function (e) {
            var vm = this
            if (vm.searchQuery == '') {
                vm.moduleddata = vm.moduleddataAll;
            } else {
                let CheckIfExist = vm.moduleddataAll.filter(item => item.moduleTitle.toLowerCase().includes(vm.searchQuery.toLowerCase()));
                vm.moduleddata = CheckIfExist;
            }
            if (vm.moduleddata.length > 0) {
                setTimeout(function () {
                    vm.activateTab(vm.moduleddata[0]);
                }, 100);
            }
        },
        Getoption: function (params) {
            params = params == null ? "" : params;
            var options = [];
            $.each(params.split("^"), function (i, v) {
                options.push(v);
            });
            return options;
        },
        async saveStorageConfiguration() {
            debugger;
            var vm = this;
            vm.objData = [];
            var dataString = '';
            var finalString = ''
            vm.isLoading = true
            //var params = `keyValue=&moduleId=${vm.activeKey}&IsWizard=`;
            var textValues = $('div#ulKeysContainer input:text,div#ulKeysContainer select,div#ulKeysContainer textarea,div#ulKeysContainer input:password'); //get all textboxes,textarea and drop down list from config list
            var checkBoxValues = $('div#ulKeysContainer div[type="checkbox"], div#ulKeysContainer div[type="radiobutton"]');
            for (var i = 0; i < textValues.length; i++) {
                if ($(textValues[i]).attr('keys') != undefined) {
                    if ($(textValues[i]).val() != null) {
                        var textValue = $(textValues[i]).val().trim();
                    }
                    dataString = $(textValues[i]).attr('keys') + '||' + textValue + '||' + $(textValues[i]).attr('keyName');
                }
                finalString += dataString + '::'
            }
            for (var i = 0; i < checkBoxValues.length; i++) {
                var getSelectedvalues = '';
                if ($(checkBoxValues[i]).attr('keys') != undefined && $(checkBoxValues[i]).attr('keys') != 'undefined') {
                    if ($(checkBoxValues[i]).attr('type') == 'radiobutton') {
                        var combinedName = ("config" + i);
                        //getSelectedvalues = $("input[name='"+combinedName+"']:checked").val();
                        getSelectedvalues = $(checkBoxValues[i]).find('input:radio:checked').val();
                        dataString = $(checkBoxValues[i]).attr('keys') + '||' + getSelectedvalues + '||' + $(checkBoxValues[i]).attr('keyName')
                    }
                    let radioConfigDatas = {
                        DataId: $(checkBoxValues[i]).attr('keys'),
                        DataValue: getSelectedvalues
                    }
                    vm.objData.push(radioConfigDatas);
                    let jsonObject = vm.objData.map(JSON.stringify);
                    let uniqueSet = new Set(jsonObject);
                    vm.allConfigObject = Array.from(uniqueSet).map(JSON.parse);
                }
                finalString += dataString + '::'
            }
            // var imageControl = $('#flFile').val();
            // var splitimage = imageControl.split('\\')[2];
            // if (splitimage != undefined && vm.defaultCompanyLogo == null) {
            //     dataString = vm.CompanyLogoCoreConfigId + '||' + imageControl +'||'+ 'NA'
            //     finalString += dataString + '::'
            // } else if (vm.defaultCompanyLogo != null) {
            //     dataString = vm.CompanyLogoCoreConfigId + '||' + 'COMPANY-LOGO.JPG'+'||'+ + 'NA'
            //     finalString += dataString + '::'
            // }
            var params = `moduleId=${this.activeKey}&keyValue=${finalString}&IsWizard=`;
            await DataService.ModuleValidationMessages(params).then((response) => {
                vm.validate = response.data.status
                if (vm.validate == 0) {
                    vm.update(this.activeKey, finalString);
                }
                vm.isLoading = false
            })

            //})
        },
        async update(Id, string) {
            var vm = this;
            vm.isLoading = true
            var params = `moduleId=${Id}&keyValue=${string}`
            await DataService.UpdateCompanySetupConfig(params).then((response) => {

                vm.saveArray = response.data
                if (vm.saveArray.status == 1) {
                    vm.isLoading = false
                    vm.ShowAlert(vm.$t("RecordsSavedSuccessfully"), 'success', true, vm.$t('Alert'));
                } else {
                    vm.isLoading = false
                }

            })
        },
        async GetLeftModulesdata() {
            var vm = this;
            await DataService.GetAllModule().then((response) => {
                vm.moduleddata = response.data;
                vm.moduleddataAll = vm.moduleddata;
                vm.moduleddata.forEach((item) => {
                    switch (item.moduleTitle) {
                        case "Approval":
                            item.classicon = "fa fa-thumbs-up action-icon";
                            break;
                        case "Asset Management":
                            item.classicon = "fa fa-thumbs-up action-icon";
                            break;
                        case "Client Bill":
                            item.classicon = "fa fa-sticky-note action-icon";
                            break;
                        case "Clients":
                            item.classicon = "fa fa-users action-icon";
                            break;
                        case "Cloud Storage":
                            item.classicon = "fa fa-database action-icon";
                            break;
                        case "Customer Relationship Management":
                            item.classicon = "fa fa-tasks action-icon";
                            break;
                        case "Emails":
                            item.classicon = "fa fa-envelope action-icon";
                            break;
                        case "Expense Approval":
                            item.classicon = "fa fa-check action-icon";
                            break;
                        case "Expenses":
                            item.classicon = "fa fa-money action-icon";
                            break;
                        case "General":
                            item.classicon = "fa fa-cog action-icon";
                            break;
                        case "Hiring":
                            item.classicon = "fa fa-list-alt action-icon";
                            break;
                        case "Knowledge Base":
                            item.classicon = "fa fa-book action-icon";
                            break;
                        case "Outlook Integration":
                            item.classicon = "fa fa-thumbs-up action-icon";
                            break;
                        case "Project files":
                            item.classicon = "fa fa-folder-open action-icon";
                            break;
                        case "Project Message Board":
                            item.classicon = "fa fa-comment action-icon";
                            break;
                        case "Projects":
                            item.classicon = "fa fa-folder-open action-icon";
                            break;
                        case "Quotation":
                            item.classicon = "fa fa-file-text action-icon";
                            break;
                        case "Rmm":
                            item.classicon = "fa fa-thumbs-up action-icon";
                            break;
                        case "Ticketing":
                            item.classicon = "fa fa-street-view action-icon";
                            break;
                        case "Time Tracker":
                            item.classicon = "fa fa-clock-o action-icon";
                            break;
                        case "Timesheet":
                            item.classicon = "fa fa-calendar action-icon";
                            break;
                        case "Training":
                            item.classicon = "fa fa-pencil-square action-icon";
                            break;
                        case "Work Diary":
                            item.classicon = "fa fa-picture-o action-icon";
                            break;
                        default:
                            item.classicon = "fa fa-pencil";
                    }
                });
                if (vm.moduleddata.length > 0) {
                    setTimeout(function () {
                        vm.activateTab(vm.moduleddata[0]);
                    }, 100);
                }
            });
        },
        async activateTab(item) {
            debugger;
            var vm = this;
            vm.dynamicclass = item.classicon
            vm.showsavebutton = true;
            this.modulename = item.moduleTitle;
            this.activeKey = item.moduleId;
            this.selectedModule = item;

            var params = `moduleId=${vm.activeKey}&IsWizard=`;
            await DataService.GetKeys(params).then((response) => {
                if (response.data.keyList.length > 0) {
                    debugger
                    vm.moduleKeys = response.data.keyList
                    vm.imgPath = vm.moduleKeys[5].coreConfigValue
                    vm.CompanyLogoCoreConfigId = vm.moduleKeys[5].coreConfigId;
                    vm.GetBlobUrl();
                    vm.GetTimeZone();

                } else {

                }

            })
        },
    },

};
</script>

<style>
.popover-content {
    position: absolute;
    background-color: #fff;
    border: 1px solid #ccc;
    padding: 10px;
    /* Add more styling as needed */
}

.left-menu.active {
    background-color: #e0e0e0;
    /* Add any other styles for the active class */
}
</style>
